import Vue from 'vue'
import Router from 'vue-router'
import Work from '../components/Work'

import Home from '../components/Home'
import Resume from '../components/Resume'
import Contact from '../components/Contact'

import userSwitcher from '../customer_portal/userSwitcher'

import CTUI from '../case_studies/ctui'
import selfService from '../case_studies/selfService'
import mobCTUI from '../case_studies/mob_ctui'


import globalSearch from '../internal_platform/globalSearch'
import originPlatform from '../internal_platform/originPlatform'
import computerTelephony from '../internal_platform/computerTelephony'

import serviceBlueprint from '../service_design/service_blueprint'
import CEI from '../service_design/cei'
import heuristicAnalysis from '../service_design/heuristic_analysis'
import experienceMap from '../service_design/experience_story_map'

Vue.use(Router)



export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/work',
      name: 'work',
      component: Work
    },
    {
      path:'/resume',
      name:'Resume',
      component: Resume
    },
    {
      path:'/contact',
      name:'Contact',
      component: Contact
    },
    {
      path:'/work/user_switcher',
      name:'userSwitcher',
      component: userSwitcher
    },
    {
      path:'/work/computer-telephony',
      name:'computerTelephony',
      component: computerTelephony
    },
    {
      path:'/work/consolidated_ticketing',
      name:'ctui',
      component: CTUI
    },
    {
      path:'/work/mobile_ticketing',
      name:'mobCTUI',
      component: mobCTUI
    },
    {
      path:'/work/global_search',
      name:'globalSearch',
      component: globalSearch
    },
    {
      path:'/work/origin_platform',
      name:'originPlatform',
      component: originPlatform
    },
    {
      path:'/work/self_service',
      name:'selfService',
      component: selfService
    },
    {
      path:'/work/service_blueprint',
      name:'Service Blueprint',
      component: serviceBlueprint
    },
    {
      path:'/work/cei',
      name:'Customer Experience Index',
      component: CEI
    },
    {
      path:'/work/heuristic_analysis',
      name:'Heuristic Analysis',
      component: heuristicAnalysis
    },
    {
      path:'/work/experience_story_map',
      name:'Experience Story Map',
      component: experienceMap
    }
    ],
    scrollBehavior (to, from, savedPosition) {
      if (savedPosition) {
        return { x: 0, y: 0 }
      } else {
        return { x: 0, y: 0 }
      }
    }
})